"use client";

import * as React from "react";
import { useParams, useRouter } from "next/navigation";
import type { QuestionnaireAnswers } from "@/app/components/questionnaire";
import { LandingPagePreview } from "@/app/components/landing-page-preview";

const keyOf = (slug: string) => `donepage:answers:${slug}`;

export default function SlugPage() {
  const params = useParams<{ slug: string }>();
  const router = useRouter();
  const slug = params?.slug;

  const [answers, setAnswers] = React.useState<QuestionnaireAnswers | null>(null);
  const [ready, setReady] = React.useState(false);

  React.useEffect(() => {
    if (!slug) return;

    const raw = localStorage.getItem(keyOf(slug));
    if (!raw) {
      setAnswers(null);
      setReady(true);
      return;
    }

    try {
      setAnswers(JSON.parse(raw));
    } catch {
      setAnswers(null);
    } finally {
      setReady(true);
    }
  }, [slug]);

  if (!ready) return null;

  if (!answers) {
    return (
      <main className="min-h-screen flex items-center justify-center p-6">
        <div className="text-center">
          <div className="text-xl font-semibold">Page not found</div>
          <div className="mt-2 text-sm text-gray-600">
            This link was generated in this browser session. Please generate again.
          </div>
          <button
            className="mt-6 h-11 rounded-xl bg-black px-6 text-white"
            onClick={() => router.push("/generator")}
          >
            Back to generator
          </button>
        </div>
      </main>
    );
  }

  return (
    <LandingPagePreview
      answers={answers}
      onEdit={() => router.push("/generator")}
      mode="export"
    />
  );
}
